import * as jose from 'jose'

export async function verifyJwt(ljwt, lpubkey) {
    let out = "";
    if (ljwt && lpubkey) {
        const alg = 'RS256';
        const spki = lpubkey;
        const publicKey = await jose.importSPKI(spki, alg);
        try {
            const { payload  } = await jose.jwtVerify(ljwt, publicKey);
            out = payload.version;
        } catch (err) {
            out = "FAILED";
            console.log("FAILED");
        }
    }
    return out;
}
