import { useState, useEffect } from "react";
import { verifyJwt } from "../enc/jwt";

const Version = props => {
    const [version, setVersion] = useState("");
    useEffect(() => {
        async function getVersion() { 
            const lversion = await verifyJwt(props.db.d4, props.db.d5);
            setVersion(lversion);
        };
        if (!version) {
            getVersion();
        }
    }, [props.db.d4, props.db.d5, version, setVersion]);
    
    return (
        <div>
            <span className="version">{version}</span>
        </div>
    );
}

export default Version;